import {
  MenuItem,
  Select,
  TextField,
  Typography,
  InputLabel,
  Box,
  FormControl,
} from "@material-ui/core";
import { useState } from "react";
import "./App.css";
import { renderChart, TypeToChart } from "./chartRender/chart-render";
import StatsCalculator from "./stats-funcs/stats-calculator";

function App() {
  const [input, setInput] = useState("");
  const [chartType, setChartType] = useState("");
  const {frequencyArr, arr, ...stats} = StatsCalculator(input);
  return (
    <div className="App">
      <Box>
        <FormControl>
          <Box flexDirection="row">
            <TextField
              type="text"
              onChange={(e) => {
                setInput(e.target.value);
              }}
              value={input}
              placeholder="Data"
            />
            <TextField
              fullWidth
              label="Chart Type"
              select
              id="chart type"
              value={chartType}
              onChange={(e) => {
                setChartType(e.target.value);
              }}
            >
              <MenuItem value="line">Line Chart</MenuItem>
              <MenuItem value="histogram">Histogram</MenuItem>
            </TextField>
          </Box>
        </FormControl>
      </Box>
      <ol>
        {input &&
          Object.entries(stats).map(([key, value]) => <div>{`${key}: ${value}`}</div> )}
      </ol>
      {input && chartType && (
        <TypeToChart frequencyArr={frequencyArr} rawArr={arr} chartType={chartType} />
      )}
    </div>
  );
}

export default App;
